@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background: url('../gorgeous-clouds-background-with-blue-sky-design_1017-25501.png') no-repeat center/cover;
  width: 100%;
  font-family: "Poppins", "Helvetica";
}

.img {
  background: url('../nigh-clouds.png') no-repeat center/cover;
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.35);
}

.flex {
  display: flex;
  justify-content:center;
  align-items:center;
  flex-direction: column;
}

.App {
  height: 100vh;
}

.container {
  /* border-radius: 2rem; */
  /* background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0)); */
  /* box-shadow: 0 0 10px rgb(255, 255, 255); */
  padding: 3rem;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#current {
  border-radius: 2rem;
  padding-bottom: 1rem;
  min-width: 45rem;
  min-height: 20rem;
  padding: 0.5rem 1.5rem 2rem 1.5rem;
  justify-content: space-evenly;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(20px);
  box-shadow: 0 0 8px rgb(255, 255, 255);
}

h3 {
  /* margin-bottom: 1.5rem; */
  font-size: 1.6rem;
  text-transform: capitalize;
  color: #fff;
}

h4 {
  margin-top: 1rem;
}

#desc {
  font-size: 1.8rem;
}
h2 {
  font-size: 2rem;
  color: #fff;
}

.stats {
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
}

#main {
  /* font-size: 3rem; */
  color: #fff;
  /* margin-left: 1.4rem; */
  text-align: center;
}

#main p {
  font-size: 2.2rem;
}

.metric {
  color: #fff;
}
p {
  font-size: 1.6rem;
}

.stats h3 {
  /* margin-left: 0.8rem; */
  margin-bottom: 0.3rem;
}

.metr{
  font-size: 1.2rem;
  box-shadow: 0 0 10px rgb(255, 255, 255);
  border-radius: 0.5rem;
  padding: 0 2rem;
  margin-left: 1rem;
}

.selected {
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
}

.not-selected {
  color: #ccc;
  font-size: 1.5rem;
  cursor: pointer;
  box-shadow: 0 0 4px rgb(255, 255, 255);
}

#slash {
  font-size: 1.5rem;
  color: #eee;
}

.side {
  color: #eee;
  /* text-align: center; */
}

.side p {
  font-size: 1.8rem;
}

#title {
  grid-column: span 4;
  text-align: center;
  margin-top: 0.5rem;
}
#forecast {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(20px);
  box-shadow: 0 0 8px rgb(255, 255, 255);
  min-width: 60rem;
  min-height: 12rem;
  border-radius: 2rem;
  margin-top: 3rem;
  padding: 1.5rem 0;
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-template-rows: auto;
  /* grid-auto-flow: column; */
  row-gap: 1.5rem;
}

.hourly--data {
  /* margin: 1rem 0 0 3rem; */
  color: #fff;
  border-right: 1px solid #fff;
  /* border-bottom: 1px solid #fff; */
  margin-top: 1rem;
}

.hourly--data:nth-child(5n), .hourly--data:last-child {
  border-right: none;
}
#search {
  display: flex;
  justify-content:center;
  align-items:center;
  margin-bottom: 2.5rem;
  z-index: 10;
  width: 30rem;
}

input {
  width: 100%;
  border-radius: 5rem 0 0 5rem;
  padding: 1rem;
  border: none;
  /* margin-right: 1rem; */
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  color: #fff;
  text-indent: 10px;
}
hr {
  width: 50%;
}
input:hover {
  box-shadow: 0 0 4px rgb(255, 255, 255);
}

input::placeholder {
  color: #ddd;
}

input:focus {
  outline: none;
  box-shadow: 0 0 10px rgb(255, 255, 255);
}

button {
  background-color: #0062ff;
  color: #fff;
  border: none;
  padding: 1rem;
  border-radius: 0 5rem 5rem 0;
  cursor: pointer;
  width: 20%;
}

button:active {
  background-color: rgb(0, 80, 209);
}

.about {
  height: 100vh;
  width: 100%;
  background: url('../about.png') no-repeat center/cover;
  color: #ddd;
 
  display: flex;
  justify-content:center;
  align-items:center;
  flex-direction: column;
}

.about > * {
  z-index: 10;
  /* margin: 1.5rem; */
}

#about {
   padding: 20rem;
}

h1{
  font-size: 5rem;
}

.about::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  backdrop-filter: blur(5px);
  background: rgba(0, 0, 0, 0.35);
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 8vh;
  border-bottom: 1px solid #ccc;
  z-index: 50;
}

li {
  list-style: none;
  display: inline;
  margin: 0 5rem 0 5rem;
}

li > * {
  text-decoration: none;
  font-size: 2rem;
  color: #fff;
}


